<template>
	<view class="listcard" @click="open">
		<view>
			<view class="liscard-head">
				<view class="liscard-personal_faces">
					<image :src="item.face" lazy-load mode="widthFix"></image>
				</view>

				<view class="liscard-personal_msg">
					<view class="liscard-personal_name">{{item.name}}</view>
					<view class="liscard-personal_time">{{item.time}}</view>
				</view>
				<view class="listcard-tag_type">{{item.type}}</view>
				<view class="listcard-tag_reward">{{item.reward}}积分</view>
			</view>
			<text class="listcard-content">{{item.content}}</text>
			<!-- <view class="listcard-img">
				<image src='../../static/touxiang.jpg'></image>
				<image src='../../static/touxiang.jpg'></image>
				<image src='../../static/touxiang.jpg'></image>
			</view> -->
			<view class="listcard-act" @click.stop>

				<view class="listcard-act_thumbsup">
					<uni-icons type="hand-thumbsup"></uni-icons>
					<text>{{item.thumbsup}}</text>
				</view>
				<view class="listcard-act_redo">
					<uni-icons type="redo"></uni-icons>
					<text>{{item.redo}}</text>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	export default {
		props: {
			item: {
				type: Object,
				default () {
					return {}
				}
			}
		},
		data() {
			return {

			};
		},
		methods: {
			open() {
				const item = this.item
				this.$emit('click', item)
				const params = {
					face: item.face,
					name: item.name,
					time: item.time,
					thumbsup: item.thumbsup,
					content: item.content,
					number: item.number,
					reward: item.reward,
					type: item.type
				}
				uni.navigateTo({
					url: '/pages/home-detail/home-detail?params=' + JSON.stringify(params)
				})
			}
		}

	}
</script>

<style lang="scss">
	.listcard {
		display: flex;
		padding: 15px;
		margin: 10px;
		border-radius: 5px;
		box-shadow: 0 0 5px 1px rgba($color: #000000, $alpha: 0.1);
		box-sizing: border-box;
		flex-direction: column;

		.liscard-head {
			display: flex;
			// justify-content: space-between;
			align-items: center;

			.listcard-tag_type,
			.listcard-tag_reward {
				align-self: flex-start;
				font-size: 12px;
				line-height: 1.5;
				margin: 0 5px;
				padding: 0 5px;
				color: $mk-base-color;
				border: 1px $mk-base-color solid;
				border-radius: 15px;
			}

			.liscard-personal_faces {
				width: 40px;
				height: 40px;
				border-radius: 50%;
				overflow: hidden;
				display: flex;
				align-items: center;
				margin-right: 15px;

				image {
					width: 100%;
					height: 100%;
				}
			}

			.liscard-personal_msg {
				.liscard-personal_name {
					font-size: 14px;
					line-height: 1.5;
				}

				.liscard-personal_time {
					font-size: 12px;
					line-height: 1.5;
					color: #999;
				}
			}

		}

		.listcard-content {
			display: flex;
			font-size: 16px;
			margin-top: 10px;
			width: 100%;
			word-break: break-all;
		}

		// .listcard-img {
		// 	display: flex;
		// 	justify-content: space-between;
		// 	image{
		// 		width: 50px;
		// 		height: 50px;
		// 	}
		// }

		.listcard-act {
			display: flex;
			justify-content: flex-end;

			.listcard-act_thumbsup,
			.listcard-act_redo {
				margin: 0 10px;

				&text {
					margin: 0 10px;
				}
			}

		}
	}
</style>
